import React, { useEffect } from "react";
import { CircularProgress } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";

import { fetchArticles, selectArticle } from "../../redux/actions";
import ViewArticle from "./ViewArticle";
import NoRecords from "../common/NoRecords";

const useStyles = makeStyles(() => ({
    root: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "calc(100vh - 32px)",
        backgroundColor: "white",
        width: "calc(100vw - 16px - 256px - 16px - 16px - 400px - 16px - 16px)",
        marginLeft: 256 + 16 + 16,
        borderRadius: 16,
        paddingTop: 32,
        paddingBottom: 32,
    },
    pageTitle: {
        fontSize: 40,
        marginTop: 32,
        fontWeight: 600,
    },
    progress: {
        width: 40,
        height: 40,
    },
    seperator: {
        height: 0.5,
        width: "calc(100% - 96px)",
        backgroundColor: "#343A4030",
        marginTop: 16,
        marginBottom: 16,
    },
}));

const ViewArticles = () => {
    const classes = useStyles();
    const { list, fetching } = useSelector((state) => state.articles);
    const { firstName } = useSelector((state) => state.session.payload);
    const dispatch = useDispatch();
    const history = useHistory();

    useEffect(() => {
        dispatch(fetchArticles());
    }, [dispatch]);

    const handlePlay = (article) => () => {
        dispatch(selectArticle(article));
    };

    const handleEdit = (article) => () => {
        history.push(`/articles/${article.id}/edit`);
    };

    const handleNewArticle = () => {
        history.push("/articles/new");
    };

    return (
        <div>
            <div className={classes.root}>
                {!fetching &&
                    list.length > 0 &&
                    list.map((item) => (
                        <React.Fragment key={item.id}>
                            <ViewArticle
                                title={item.title}
                                description={item.description}
                                author={`${`${item.author.firstName} ${item.author.lastName}`}`}
                                narrator={`${`${item.narrator.firstName} ${item.narrator.lastName}`}`}
                                image={item.imageURLs[0]}
                                onPlay={handlePlay(item)}
                                onEdit={handleEdit(item)}
                            />
                            <div className={classes.seperator} />
                        </React.Fragment>
                    ))}
                {!fetching && list.length === 0 && (
                    <NoRecords
                        message={`Are you ready to create your first article, ${firstName}?`}
                        actionText="Create New Article"
                        actionHandler={handleNewArticle}
                        image={`${process.env.PUBLIC_URL}/assets/images/empty-articles.svg`}
                        action={true}
                    />
                )}
                {fetching && <CircularProgress className={classes.progress} />}
            </div>
        </div>
    );
};

export default ViewArticles;
